@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;1,400&family=Playfair+Display:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MuiTableCell-root {
  font-family: 'Montserrat', sans-serif !important;
}



.App {
  text-align: center;
}

.header-title {
  font-family: 'Playfair Display', serif;
  font-weight: 400;
  font-size: calc(16px + 5.5vmin);
  margin: auto;
  margin-top: 2%;
  padding: 15px;
  width: 80%;
}

.ivar-logo {
  width: calc(50px + 6vmin);
  display:inline-block;
  margin-bottom: 1%;
}

.ornament {
  width: calc(50px + 20vmin);
  display:inline-block;
}
  

.rule-header h2 {
    font-family: 'Playfair Display', serif;
    font-size: calc(14px + 0.5vmin);
}

.rule-box {
    font-family: 'Montserrat', sans-serif;
    text-align: left;
    padding: 0 25px 25px 25px;
    font-size: 16px;
}

.rule-box p {
    text-align: justify;
}

.rule-header {
    color: white;
    background-color: black;
    text-align: left;
    padding-left: 25px;
}

table {
    table-layout: fixed;
    width: 100%;
}

table, th, td {
    border: 1px solid #eaeaea;
    border-collapse: collapse;
    border-right: none;
    border-left: none;
    color: rgba(0, 0, 0, 0.87);
    font-weight: 300;
    line-height: 1.5rem;
    font-family: 'Montserrat', sans-serif !important;
    padding: 6px 24px 6px 16px;
    line-height: 1.43rem;
    font-size: 0.875rem;
}

thead, tr, th {
    font-weight: 500;
    color: rgba(0, 0, 0, 0.87);
    line-height: 1.5rem;
    padding: 14px;
}

thead {
    background-color: #eaeaea;
}

blockquote {
    border-left: 2px solid #949494;
    padding-left: 10px;
}

.hash-link {
    text-decoration: none;
}

.hash-link:focus, .hash-link:hover, .hash-link:visited, .hash-link:link, .hash-link:active {
    text-decoration: none;
    color:inherit
}

@media only screen and (max-width: 1000px) {
    table {
        word-break: break-word;
        overflow-wrap: break-word;
        -webkit-hyphens: auto;
            -ms-hyphens: auto;
                hyphens: auto;
    }
}
